/*SMALL_CARD*/
.card__description {
    box-sizing: border-box;

    width: 415px;
    height: fit-content;

    display: grid;
    align-items: start;
    justify-content: start;
    align-content: center;
    justify-items: stretch;
    grid-template-columns: auto 1fr auto;
    grid-template-rows: repeat(3, auto);
    gap: 8px;
    padding: 17px 24px;

    position: relative;

    background: var(--white);
    border: 1px solid var(--gray);
    z-index: 1;
}

.text--heading {
    grid-row: 1;
    grid-column: 2;

    width: 100%;
    height: fit-content;

    font-style: normal;
    font-weight: 500;
    font-size: 17px;
    line-height: 26px;
    letter-spacing: -0.03em;

    color: var(--black);
}

.text--subheading {
    margin-top: -7px;
    grid-row: 2;
    grid-column: 2;

    width: auto;
    height: fit-content;

    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 26px;
    letter-spacing: -0.01em;

    color: var(--primary-lighter);
}

.text--status {
    grid-row: 3;
    grid-column: 2;

    width: auto;
    height: fit-content;

    font-style: normal;
    font-weight: 700;
    font-size: 15px;
    line-height: 26px;
    letter-spacing: -0.01em;

    color: var(--black);
}

.text--heading,
.text--subheading,
.text--status {
    font-family: 'Inter', serif;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.button {
    position: relative;
    grid-row: 1 / 4;
    grid-column: 3;
    align-self: center;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 17px;
    gap: 11px;

    width: 59px;
    height: 59px;

    background: var(--gray-background);
    border-radius: 999px;
    transition: background 0.45s ease-in-out;

    cursor: pointer;
}

.button:hover {
    background: var(--primary-background);
}

.button::before {
    content: url(../../assets/bookmark--primary.svg);
    position: absolute;

    width: 24px;
    height: 24px;
    margin-bottom: -5px;
    display: flex;
    justify-content: center;
    align-items: center;
}

@media(max-width: 720px) {
    .card__description {
        width: 100%;
    }
}

@media (hover: none), (pointer: coarse) {
    .button {
        transition: none;
    }

    .button:hover,
    .button:focus {
        background: var(--gray-background);
        transition: none;
    }

    .button:active {
        background: var(--primary-background);
    }
}